.search-results {
	ul {
		margin: 30px 0;
		padding: 0;
		list-style: none;
		li {
			display: flex;
			flex-direction: column;
			justify-content: center;
			background: #fff;
			padding: 20px 170px 20px 20px;
			margin: 0 0 15px 0;
			position: relative;
			border-bottom: 5px solid #067ab9;
			@media(max-width: 600px) {
				padding: 20px;
			}
			h2 {
				margin: 0 0 15px 0;
				line-height: 1.25;
			}
			p {
				margin: 0;
				font-style: italic;
			}
			a {
				position: absolute;
				right: 20px;
				top: 0;
				bottom: 0;
				margin: auto;
				padding: 0 20px;
				height: 50px;
				border-radius: 5px;
				background: #067ab9;
				color: #fff;
				font-weight: 600;
				text-decoration: none;
				text-transform: uppercase;
				text-align: center;
				line-height: 50px;
				@media(max-width: 600px) {
					display:block;
					position: static;
					margin: 15px 0 0 0;
				}
			}
		}
	}
}
